.loading_dots {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.dot {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #1c85ff;
  animation: bounce 1s infinite ease-in-out;
  animation-delay: 5ms;
}

.header {
  padding: 20px 30px;
  text-align: center;
  box-shadow: 0px 4px 4px #d3d3d3;
}

.link {
  display: inline-block;
  font-size: 24px;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.2s ease-in-out;
  color: #000000;
}

.link {
  margin-right: 40px;
}

.link:active,
.link:hover,
.link:focus {
  color: #1c85ff;
  text-decoration: underline;
}
